@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;  
  font-family:'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  margin: 0px;
}
#root {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  color: #404040;
}
.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 90%;
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  width: 50%;
  height: 56vh;
  box-shadow: inset 16px 0px 31px -14px rgba(0, 0, 0, 0.56);
  border-radius: 0 40px 40px 0;
}
.photo {
}
.img {
  width: 100%;
  color: #20aa9f;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  background-image: url(/React-Components/static/media/bg2.dc49f67b.jpg);
  height: 77vh;
  border-radius: 40px 0px 0px 40px;
  box-shadow: -24px 0px 55px -17px rgba(0, 0, 0, 0.45);
  background-color: rgba(0,0,0,0.45);
  background-blend-mode: soft-light;
  display: flex;
  flex-direction: column;
  align-items: self-end;
  justify-content: end;
  font-size: 5em;
  font-weight: 700;
}

